import { BackgroundActionType, BackgroundMessageType } from "./actions/types";
import {
    captureAndCropAction,
    captureFullPageAction,
    captureRegionAction,
    captureVisibleAction,
    doPieceOfCaptureAction,
    openEditorAction,
    prepareScreenRecordingAction,
    startDesktopRecordingAction,
    stopRecordingAction,
} from "./actions";
import { background, clearRecordAlarms, storage } from "../utils";
import { CommandsType } from "./types";
import { uploadToGoogleDriveAction } from "./actions/uploadToGoogleDriveAction";
import { DISABLE_ID } from "../../app/Popup/pages/RecordPage/const";
import { ContentActionType } from "../content/actions/types";
import { StorageKey } from "../StorageKey";
import { RecordDataType } from "../utils/types";
import { CONTENT_CONTROL_BAR_ID } from "../content/const";
// @ts-ignore
import { URLs } from 'browserConst';
import { checkIsBrowserSettingsPage } from "../../utils";

chrome.runtime.onMessage.addListener((message: BackgroundMessageType) => {
    switch (message.action){
        case BackgroundActionType.CaptureVisible: {
            captureVisibleAction();
            break;
        }
        case BackgroundActionType.CaptureFullPage: {
            captureFullPageAction();
            break;
        }
        case BackgroundActionType.CaptureRegion: {
            captureRegionAction();
            break;
        }
        case BackgroundActionType.DoPieceOfCapture: {
            doPieceOfCaptureAction(message.body);
            break;
        }
        case BackgroundActionType.OpenEditor: {
            openEditorAction(message.body);
            break;
        }
        case BackgroundActionType.CaptureAndCrop: {
            captureAndCropAction();
            break;
        }
        case BackgroundActionType.PrepareScreenRecording: {
            prepareScreenRecordingAction(message.body);
            break;
        }
        case BackgroundActionType.StartDesktopRecording: {
            startDesktopRecordingAction(message.body);
            break;
        }
        case BackgroundActionType.UploadToGoogleDrive: {
            uploadToGoogleDriveAction(message.body);
            break;
        }
    }
    return true;
});

chrome.storage.onChanged.addListener((changes) => {
    for(let key in changes) {
        if(key === StorageKey.RecordData) {
            const recordData = changes[key].newValue as RecordDataType;
            const oldRecordData = changes[key].oldValue as RecordDataType;
            if(!recordData.isRecording && oldRecordData.isRecording) {
                if(oldRecordData.isControlBar) {
                    background.sendMessage(recordData.controlBarTabId ?? 0, {
                        action: ContentActionType.RemoveContentUI,
                        body: { contentId: CONTENT_CONTROL_BAR_ID },
                    });
                }
            }
        }
    }
});

chrome.tabs.onUpdated.addListener(async function(tabId, { status, url }) {
    if (status === 'complete') {
        const { isRecording, isControlBar, controlBarTabId } = await storage.getRecordData();
        const [{ id }] = await chrome.tabs.query({ active: true });

        if (isRecording && isControlBar && controlBarTabId === id) {
            background.sendMessage(id ?? 0, {
                action: ContentActionType.AddControlBar,
                body: { tabId: id ?? 0 },
            })
        }
    }
    if (status === 'loading' && url && !(checkIsBrowserSettingsPage(url))) {
        await storage.setDefaultTabData(tabId);
        await storage.removeTabWithCamera(tabId);
    }
});

chrome.tabs.onRemoved.addListener(function(tabId) {
    storage.removeTabData(tabId);
});

chrome.tabs.onRemoved.addListener(async function(tabId) {
    const recordData = await storage.getRecordData();
    if(tabId === recordData.controlTabId) {
        await storage.setRecordData({ isRecording: false });
        await clearRecordAlarms();
        await chrome.action.setBadgeText({
            text: '',
        });
    }
});

chrome.commands.onCommand.addListener(function(command) {
    const commandType = command as CommandsType;
    chrome.tabs.update({}, async function() {
        switch (commandType) {
            case 'visible_area_capture': {
                captureVisibleAction();
                break;
            }
            case "all_page_capture": {
                captureFullPageAction();
                break;
            }
            case "start_record": {
                const { microphoneId, speakerId } = await storage.getPopupData()
                prepareScreenRecordingAction({ microphoneId, isSpeaker: speakerId !== DISABLE_ID, isCurrentTabRecording: false });
                break;
            }
            case "stop_record": {
                stopRecordingAction();
                break;
            }
        }
    });
});

chrome.runtime.onInstalled.addListener(function(details) {
    if (details.reason === 'install') {
        chrome.tabs.create({ url: URLs.website });
    }
});