import { background } from "../../utils";
import { ContentActionType } from "../../content/actions/types";

export const captureAndCropAction = () => {
    chrome.tabs.query({ active: true },  ([{ windowId, id }]) => {
        chrome.tabs.captureVisibleTab(windowId, {format: 'png', quality: 100}, imageSrc => {
            background.sendMessage(
                id ?? 0,
                {
                    action: ContentActionType.DataUrlToCanvas,
                    body: {
                        tabId: id ?? 0,
                        dataUrl: imageSrc,
                    }
                }
            );
        });
    });
}
