import { background } from "../../utils";
import { ContentActionType } from "../../content/actions/types";
import { storage } from "../../utils";

export const captureRegionAction = () => {
    chrome.tabs.query({ active: true }, ([{ id, windowId }]) => {
        chrome.windows.update(windowId ?? 0, { focused: true });
        storage.setTabProps({ isCropping: true }).then(() => {
            background.sendMessage(
                id ?? 0,
                {
                    action: ContentActionType.StartCapturingRegion,
                    body: {
                        tabId: id ?? 0,
                    },
                }
            );
        });
    });
}
