import { background, storage } from "../../utils";
import { ContentActionType } from "../../content/actions/types";
import { RecordControl } from "../../content/const";

export const stopRecordingAction = async () => {
    await storage.setRecordData({ isRecording: false });

    const recordData = await storage.getRecordData();
    background.sendMessage(
        recordData.controlTabId ?? 0,
        {
            action: ContentActionType.ControlRecordingDesktop,
            body: { action: RecordControl.Stop },
        },
    );
}
