import { UploadToGoogleDriveMessageBodyType } from "./types";
import { background, storage } from "../../utils";
import { ContentActionType } from "../../content/actions/types";
// @ts-ignore
import { CLIENT_ID } from 'browserConst';

export const uploadToGoogleDriveAction = ({ dataUrl, fileName, type, id }: UploadToGoogleDriveMessageBodyType) => {
    let auth_url = 'https://accounts.google.com/o/oauth2/auth?';
    const client_id = CLIENT_ID;
    const redirect_url = chrome.identity.getRedirectURL();

    const auth_params = {
        client_id: client_id,
        redirect_uri: redirect_url,
        response_type: 'token',
        scope: 'https://www.googleapis.com/auth/drive.file',
        login_hint: '',
    };

    const url = new URLSearchParams(Object.entries(auth_params));
    url.toString();
    auth_url += url;

    chrome.identity.launchWebAuthFlow({url: auth_url, interactive: true}, async function(responseUrl) {
        console.log(responseUrl);
        const token = responseUrl?.replace('#','x=x&')
            .split('&')
            .reduce(
                function(p,e){
                    const a = e.split('=');
                    p[decodeURIComponent(a[0])] = decodeURIComponent(a[1]);
                    return p;
                },
                {} as { [key: string]: string; }
            )['access_token'];
            if (chrome.runtime.lastError) {
                console.error(chrome.runtime.lastError);
                return;
            }

            const base64Data = dataUrl.split(',')[1];
            const mimeType = dataUrl.split('data:')[0].split(';')[0];

            const byteCharacters = atob(base64Data);
            const byteArrays = [];

            for (let offset = 0; offset < byteCharacters.length; offset += 512) {
                const slice = byteCharacters.slice(offset, offset + 512);
                const byteNumbers = new Array(slice.length);

                for (let i = 0; i < slice.length; i++) {
                    byteNumbers[i] = slice.charCodeAt(i);
                }

                const byteArray = new Uint8Array(byteNumbers);
                byteArrays.push(byteArray);
            }

            const blob = new Blob(byteArrays, { type: mimeType });
            const metadata = {
                name: fileName,
                mimeType,
            };

            const formData = new FormData();
            formData.append('metadata', new Blob([JSON.stringify(metadata)], { type: 'application/json' }));
            formData.append('file', blob);
            try {
                const [{ id: tabId }] = await chrome.tabs.query({ active: true });
                await background.sendMessage(tabId ?? 0, { action: ContentActionType.GoogleDriveUploading, id });
                const response = await fetch('https://www.googleapis.com/upload/drive/v3/files?uploadType=multipart', {
                    method: 'POST',
                    headers: {
                        Authorization: `Bearer ${token}`,
                    },
                    body: formData,
                });

                if (response.ok) {
                    const data = await response.json();
                    fetch(`https://www.googleapis.com/drive/v3/files/${data.id}/permissions`, {
                        method: 'POST',
                        mode: 'cors', // no-cors, *cors, same-origin
                        cache: 'no-cache',
                        credentials: 'same-origin',
                        headers: {
                            'Content-Type': 'application/json',
                            Authorization: `Bearer ${token}`,
                        },
                        redirect: 'follow', // manual, *follow, error
                        referrerPolicy: 'no-referrer', // no-referrer, *client
                        body: JSON.stringify({
                            role: 'reader',
                            type: 'anyone',
                        }),
                    }).then(async () => {
                        fetch(`https://www.googleapis.com/drive/v3/files/${data.id}?fields=webViewLink`, {
                            headers: {
                                Authorization: `Bearer ${token}`,
                            }
                        }).then(async response => {
                            const data = await response.json();
                            await background.sendMessage(tabId ?? 0, {
                                action: ContentActionType.GoogleDriveUploaded,
                                body: { url: data.webViewLink },
                                id,
                            });
                            if(type === 'video') {
                                await storage.changeUploadedRecord(+id, data.webViewLink);
                            }
                            if(type === 'image') {
                                await storage.changeUploadedScreenshot(+id, data.webViewLink);
                            }
                        })
                            .catch(e => console.log(e));
                    })
                } else {
                    const errorData = await response.json();
                    console.error(errorData);
                }
            } catch (error) {
                console.error(error);
            }
        });
};
