export type BackgroundMessageType =
    CaptureVisibleMessageType |
    CaptureFullPageMessageType |
    CaptureRegionMessageType |
    DoPieceOfCaptureMessageType |
    OpenEditorMessageType |
    CaptureAndCropMessageType |
    PrepareScreenRecordingMessageType |
    StartDesktopRecordingMessageType |
    UploadToGoogleDriveMessageType;

export enum BackgroundActionType {
    CaptureVisible = 'captureVisible',
    CaptureFullPage = 'captureFullPage',
    CaptureRegion = 'captureRegion',
    DoPieceOfCapture = 'doPieceOfCapture',
    OpenEditor = 'openEditor',
    CaptureAndCrop = 'captureAndCrop',
    StartDesktopRecording = 'startDesktopRecording',
    PrepareScreenRecording = 'prepareScreenRecording',
    UploadToGoogleDrive = 'uploadToGoogleDrive',
}

export type CaptureVisibleMessageType = {
    action: BackgroundActionType.CaptureVisible;
}

export type CaptureFullPageMessageType = {
    action: BackgroundActionType.CaptureFullPage;
}

export type CaptureRegionMessageType = {
    action: BackgroundActionType.CaptureRegion;
}

export type DoPieceOfCaptureMessageType = {
    action: BackgroundActionType.DoPieceOfCapture;
    body: DoPieceOfCaptureMessageBodyType;
}

export type DoPieceOfCaptureMessageBodyType = {
    countOfVerticalCaptures: number,
    verticalAnchor: number,
    clientHeight: number,
    scrollHeight: number,
    clientWidth: number,
};

export type OpenEditorMessageType = {
    action: BackgroundActionType.OpenEditor;
    body: OpenEditorMessageBodyType;
}

export type OpenEditorMessageBodyType = {
    dataUrl: string;
};

export type CaptureAndCropMessageType = {
    action: BackgroundActionType.CaptureAndCrop;
}

export type PrepareScreenRecordingMessageType = {
    action: BackgroundActionType.PrepareScreenRecording;
    body: PrepareScreenRecordingMessageBodyType;
}

export type PrepareScreenRecordingMessageBodyType = {
    microphoneId?: string;
    isSpeaker?: boolean;
    isCurrentTabRecording?: boolean;
}

export type StartDesktopRecordingMessageType = {
    action: BackgroundActionType.StartDesktopRecording;
    body: StartDesktopRecordingMessageBodyType;
}

export type StartDesktopRecordingMessageBodyType = {
    delay: number;
}

export type UploadToGoogleDriveMessageType = {
    action: BackgroundActionType.UploadToGoogleDrive,
    body: UploadToGoogleDriveMessageBodyType,
}

export type UploadToGoogleDriveMessageBodyType = {
    dataUrl: string;
    fileName: string;
    type: 'image' | 'video',
    id: string,
}