import { StorageKey } from "../../../extension/StorageKey";
import { Screenshot } from "../Screenshot";
import { StorageScreenshotType } from "../types";

export const setDataUrl = (id: number, dataUrl: string) => {
    return new Promise<boolean>((resolve, reject) => {
        try {
            chrome.storage.local.get(StorageKey.Screenshots, res => {
                const screenshots: StorageScreenshotType = res[StorageKey.Screenshots] ?? {};
                const screenshot = new Screenshot({});
                if(!screenshots[id]) {
                    reject(new Error(`We couldn't find the screenshot.`));
                }
                else {
                    screenshot.load(id).then(() => {
                        screenshot.dataUrl = dataUrl;
                        chrome.storage.local.set({
                            [StorageKey.Screenshots]: {
                                ...screenshots,
                                [id]: screenshot,
                            },
                        }).then(() => {
                            resolve(true);
                        })
                    })
                }
            })
        }
        catch (e) {
            reject(new Error(`'chrome' is not defined`));
        }
    });
}