import { RecordControl } from "../const";

export type ContentMessageType =
    StartTakingScreenshotMessageType |
    CheckIsScreenshotDoneMessageType |
    StartCapturingRegionMessageType |
    DataUrlToCanvasMessageType |
    RemoveCroppingUIMessageType |
    StartRecordDesktopMessageType |
    ControlRecordingDesktopMessageType |
    PrepareRecordingDesktopWebcamMessageType |
    AddWebcamOnATabMessageType |
    GoogleDriveUploadingMessageType |
    GoogleDriveUploadedMessageType |
    AccessQueryMessageType |
    AddControlBarMessageType;

export enum ContentActionType {
    StartTakingScreenshot = 'startTakingScreenshot',
    CheckIsScreenshotDone = 'checkIsScreenshotDone',
    StartCapturingRegion = 'startCapturingRegion',
    DataUrlToCanvas = 'dataUrlToCanvas',
    RemoveContentUI = 'removeContentUI',

    StartRecordDesktop = 'startRecordDesktop',
    ControlRecordingDesktop = 'controlRecordingDesktop',
    PrepareRecordingDesktopWebcam = 'prepareRecordingDesktopWebcam',
    AddWebcamOnATab = 'addWebcamOnATab',
    GoogleDriveUploading = 'googleDriveUploading',
    GoogleDriveUploaded = 'googleDriveUploaded',

    AccessQuery = 'accessQuery',
    AddControlBar = 'AddControlBar'
}

export type StartTakingScreenshotMessageType = {
    action: ContentActionType.StartTakingScreenshot;
}

export type PrepareRecordingDesktopWebcamMessageType = {
    action: ContentActionType.PrepareRecordingDesktopWebcam;
    body: PrepareRecordingDesktopWebcamMessageBodyType;
}

export type PrepareRecordingDesktopWebcamMessageBodyType = { tabId: number; }

export type CheckIsScreenshotDoneMessageType = {
    action: ContentActionType.CheckIsScreenshotDone;
    body: CheckIsScreenshotDoneMessageBodyType;
}

export type CheckIsScreenshotDoneMessageBodyType = {
    countOfVerticalCaptures: number,
    verticalAnchor: number,
    clientHeight: number,
    scrollHeight: number,
    clientWidth: number,
}

export type StartCapturingRegionMessageType = {
    action: ContentActionType.StartCapturingRegion;
    body: StartCapturingRegionMessageBodyType;
}

export type StartCapturingRegionMessageBodyType = { tabId: number; }

export type DataUrlToCanvasMessageType = {
    action: ContentActionType.DataUrlToCanvas;
    body: DataUrlToCanvasMessageBodyType;
}

export type DataUrlToCanvasMessageBodyType = {
    tabId: number;
    dataUrl: string;
}

export type RemoveCroppingUIMessageType = {
    action: ContentActionType.RemoveContentUI;
    body?: RemoveCroppingUIMessageBodyType;
}

export type RemoveCroppingUIMessageBodyType = { contentId?: string; }

export type StartRecordDesktopMessageType = {
    action: ContentActionType.StartRecordDesktop;
    body: StartRecordDesktopMessageBodyType;
}

export type StartRecordDesktopMessageBodyType = {
    delay: number;
    tabId: number;
}

export type ControlRecordingDesktopMessageType = {
    action: ContentActionType.ControlRecordingDesktop;
    body: ControlRecordingDesktopMessageBodyType;
}

export type ControlRecordingDesktopMessageBodyType = { action: RecordControl };

export type AddWebcamOnATabMessageType = {
    action: ContentActionType.AddWebcamOnATab;
    body: AddWebcamOnATabMessageBodyType;
}

export type AddWebcamOnATabMessageBodyType = { tabId: number };

export type GoogleDriveUploadingMessageType = {
    action: ContentActionType.GoogleDriveUploading;
    id: string;
}

export type GoogleDriveUploadedMessageType = {
    action: ContentActionType.GoogleDriveUploaded;
    body: GoogleDriveUploadedMessageBodyType;
    id: string;
}

export type GoogleDriveUploadedMessageBodyType = { url: string };

export type AccessQueryMessageType = {
    action: ContentActionType.AccessQuery;
    body: AccessQueryMessageBodyType;
}

export type AccessQueryMessageBodyType = { tabId: number };

export type AddControlBarMessageType = {
    action: ContentActionType.AddControlBar;
    body: AddControlBarMessageBodyType;
}

export type AddControlBarMessageBodyType = { tabId: number };