import { StorageKey } from "../../../extension/StorageKey";

export const getLastScreenshotId = () => {
    return new Promise<{ id: number }>((resolve, reject) => {
        try {
            chrome.storage.local.get(StorageKey.LastScreenshot, res => {
                const screenshotId: number | null = res[StorageKey.LastScreenshot] ?? null;
                if(screenshotId){
                    resolve({ id: screenshotId })
                }
                else {
                    reject(new Error(`There is no screenshot yet`));
                }
            })
        }
        catch (error) {
            reject(new Error(`'chrome' is not defined`));
        }
    });
}