import { StartDesktopRecordingMessageBodyType } from "./types";
import { msToMinutes } from "../../../utils";
import moment from "moment";
import { background, storage } from "../../utils";
import { AlarmName } from "../../content/const";
import { ContentActionType } from "../../content/actions/types";

export const startDesktopRecordingAction = async ({ delay }: StartDesktopRecordingMessageBodyType) => {
    const newDelay = delay - 1000;
    let recordedMs = 0;
    const { lastOpenedTabId } = await storage.getRecordData();
    background.sendMessage(
        lastOpenedTabId ?? 0,
        {
            action: ContentActionType.StartRecordDesktop,
            body: {
                tabId: lastOpenedTabId ?? 0,
                delay,
            },
        },
    );

    await chrome.alarms.create(AlarmName.RecordDelay, {
        delayInMinutes: msToMinutes(newDelay),
    });

    chrome.alarms.onAlarm.addListener((alarm) => {
        const alarmName = alarm.name as AlarmName;
        switch(alarmName) {
            case AlarmName.RecordDelay: {
                chrome.alarms.create(AlarmName.BudgeTime, {
                    periodInMinutes: msToMinutes(1000),
                });
                break;
            }
            case AlarmName.BudgeTime: {
                chrome.action.setBadgeText({
                    text: moment(recordedMs).format("mm:ss"),
                });
                recordedMs += 1000;
                break;
            }
        }
    });
}
