import { Screenshot } from "../Screenshot";
import { StorageKey } from "../../../extension/StorageKey";

export const saveScreenshot = (value: Screenshot) => {
    return new Promise<{ isSaved?: boolean }>((resolve, reject) => {
        if(chrome) {
            chrome.storage.local.get(StorageKey.Screenshots, (res) => {
                const screenshots = res[StorageKey.Screenshots] ?? {};
                value.isSaved = true;
                chrome.storage.local.set({
                    [StorageKey.Screenshots]: {
                        ...screenshots,
                        [value.id]: value,
                    }
                }).then(() => {
                    resolve({ isSaved: value.isSaved });
                })
            })
        }
        else {
            reject(new Error(`'chrome' is not defined`));
        }
    })
}