import { background } from "../../utils";
import { ContentActionType } from "../../content/actions/types";
import { DoPieceOfCaptureMessageBodyType } from "./types";
import { StorageKey } from "../../StorageKey";

export const doPieceOfCaptureAction = (body: DoPieceOfCaptureMessageBodyType) => {
    chrome.tabs.query({ active: true },  ([{ windowId, id }]) => {
        chrome.tabs.captureVisibleTab(windowId, {format: 'jpeg', quality: 100}, imageSrc => {
            chrome.storage.local.get(StorageKey.TempImage, res => {
                const tempImage: string[] = res[StorageKey.TempImage] ?? [];
                tempImage.push(imageSrc);
                chrome.storage.local.set({
                    [StorageKey.TempImage]: tempImage,
                }).then(() => {
                    background.sendMessage(
                        id ?? 0,
                        {
                            action: ContentActionType.CheckIsScreenshotDone,
                            body: {
                                ...body,
                                verticalAnchor: body.verticalAnchor + 1,
                            }
                        },
                    );
                });
            });
        });
    });
}
