import { storage } from "../../utils";
import { RoutePath } from "../../../app/Main/const";
import { PrepareScreenRecordingMessageBodyType } from "./types";

export const prepareScreenRecordingAction = async (body: PrepareScreenRecordingMessageBodyType) => {
    await chrome.tabs.query({ active: true }, async function ([currentTab]) {
        await storage.setRecordData({ lastOpenedTabId: currentTab.id, ...body });
        await chrome.tabs.create({
            url: chrome.runtime.getURL(RoutePath.Recorder),
            pinned: true,
            active: true,
        });
    });
}
