import { Screenshot } from "../../../classes/Screenshot/Screenshot";
import { RoutePath, RoutePathParam } from "../../../app/Main/const";
import { OpenEditorMessageBodyType } from "./types";

export const openEditorAction = ({ dataUrl }: OpenEditorMessageBodyType) => {
    chrome.tabs.query({ active: true },  ([{ title }]) => {
        const screenshot = new Screenshot({
            dataUrl,
            title,
        });
        Screenshot.setLastScreenshot(screenshot).then(isSaved => {
            isSaved && chrome.tabs.create({url: chrome.runtime.getURL(`${RoutePath.Editor}&${RoutePathParam.ContentId}=${screenshot.id}`)});
        });
    });
}
