import {
    saveScreenshot,
    removeScreenshot,
    loadScreenshot,
    getLastScreenshotId,
    setLastScreenshot,
    getAllScreenshots,
    removeAllScreenshots,
    getScreenshotById,
    setDataUrl,
} from "./utils";
import { IScreenshotProps, ScreenshotPropsType } from "./types";
import { getFormatFromDataUrl, getSizeFromDataUrl } from "../../utils";
import moment from 'moment';

interface IScreenshot extends IScreenshotProps {
    isSaved?: boolean;
    save: () => Promise<{isSaved?: boolean}>;
    load: (id: number) => Promise<boolean>;
}

export class Screenshot implements IScreenshot{
    private _dataUrl?: string = '';
    private _id: number;
    private _isSaved: boolean = false;
    private _title: string;
    private _isUploaded: boolean | undefined;
    private _url: string | undefined;

    constructor(data: ScreenshotPropsType) {
        this._id = Date.now();
        this._title = data.title ? data.title : 'New screenshot';
        this.dataUrl = data.dataUrl;
    }

    get id() {return this._id;}

    get dataUrl() {return this._dataUrl;}
    set dataUrl(value) {this._dataUrl = value;}

    get title() {return this._title;}
    set title(value) {this._title = value;}

    get isSaved() {return this._isSaved;}
    set isSaved(value) {this._isSaved = value;}

    get isUploaded() {return this._isUploaded;}
    set isUploaded(value) {this._isUploaded = value;}

    get url() {return this._url;}
    set url(value) {this._url = value;}

    get date() {
        const format = "DD.MM.YYYY HH:mm";
        return moment(this.id).format(format);
    }

    get format() {
        if(this.dataUrl) {
            return getFormatFromDataUrl(this.dataUrl) ?? '???';
        }
        return '???'
    }

    get fileName() {return `${this.title} (${this.date}).${this.format}`}

    get size() {return getSizeFromDataUrl(this.dataUrl ?? '')}

    save() {
        return saveScreenshot(this);
    }

    load(id: number) {
        return loadScreenshot(id).then(({screenshot}) => {
            this.copy(screenshot);
            this._isSaved = true;
            return true;
        });
    }

    copy(value: Screenshot) {
        this._id = value._id;
        this.dataUrl = value._dataUrl;
        this.isSaved = value._isSaved;
        this.title = value._title;
        this.isUploaded = value._isUploaded;
        this.url = value._url;
    }

    static delete(id: number) {
        return removeScreenshot(id);
    }

    static async deleteByIds(ids: number[]) {
        for (const id of ids) {
            await removeScreenshot(id);
        }
        return true;
    }

    static deleteAll() {
        return removeAllScreenshots();
    }

    static getLastScreenshot() {
        return getLastScreenshotId().then(({ id }) => {
            const screenshot = new Screenshot({});
            return screenshot.load(id).then(() => {
                return screenshot;
            })
        });
    }
    static setLastScreenshot(value: Screenshot) {
        return value.save().then(() => {
            return setLastScreenshot(value)
        });
    }
    static getAllScreenshots() {
        return getAllScreenshots().then(value => {
            const screenshots: Screenshot[] = [];
            for (let key in value){
                const screenshot = new Screenshot({});
                screenshot.copy(value[key]);
                screenshots.push(screenshot);
            }
            return screenshots.reverse();
        });
    }

    static getScreenshotById(id: number) {
        return getScreenshotById(id).then(screenshot => {
            const result = new Screenshot({});
            result.copy(screenshot);
            return result;
        });
    }

    static uploadDataUrl(id: number, dataUrl: string) {
        return setDataUrl(id, dataUrl);
    }
}